.post{
	background-color: #F8F8FF;
	width: 18em;
	height: auto;
    border-radius: 5px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
}

.text-distance{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-positive: 1;
        flex-grow: 1;
}
.post-text{
    width: 75%;
}

.post-text p{
    font-size: 1em;
    color: #000000;
}

.post-distance{
    width: 40%;
    color: #000000;
}

.post-distance p{
    -ms-flex-positive: 1;
        flex-grow: 1;
    font-size: 0.8em;
    color: 	#808080;
}

.post-button{
    text-align: right;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: center;
        justify-content: center;
}

.post-button img{
    width: 100%;
}



