        flex-direction: row;
    -ms-flex-pack: center;
        justify-content: center;
	margin-top: 10px;
	text-align: center;
}

.picture{
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.2);
}

.profile_edit{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.profile_edit img{
    width: 4em;
    height: 4em;
    -ms-flex-pack: end;
        justify-content: flex-end;
}

img{
    width: 100%;
    height: auto;
}

.description p{
    font-size: 1em;
}

.description a{
    font-size: 1em;
    color: #F8F8FF;
}

.bar {
    fill: #F8F8FF;
    height: 21px;
    -webkit-transition: fill .3s ease;
    -o-transition: fill .3s ease;
    transition: fill .3s ease;
    cursor: pointer
}


